/**
 * @file export navigate api
 */

import {callback} from '../../lib/utils';

function guid() {
    return Math.random().toString(36).substr(2, 12);
}

/**
 *  tabbar 
 * @param {Object} options 
 * @param {string} options.url  tabBar 
 * @param {Function} options.success 
 * @param {Function} options.fail 
 * @param {Function} options.complete 
 */
function navigateTo(options) {
    let {
        url,
        success,
        fail,
        complete
    } = options;
    /* eslint-disable fecs-camelcase */
    this.$router && this.$router.push({
        path: url,
        query: {
            '_': guid()
        }
    }, function (res) {
        callback(success, res);
        callback(complete, res);
    }, function (res) {
        callback(fail, res);
        callback(complete, res);
    });
    /* eslint-enabel fecs-camelcase */
}

/**
 * 
 * @param {Object} options 
 * @param {string} options.url  tabBar 
 * @param {Function} options.success 
 * @param {Function} options.fail 
 * @param {Function} options.complete 
 */
function redirectTo(options) {
    let {
        url,
        success,
        fail,
        complete
    } = options;
    this.$router && this.$router.replace(url, function (res) {
        callback(success, res);
        callback(complete, res);
    }, function (res) {
        callback(fail, res);
        callback(complete, res);
    });
}

/**
 * 
 * @param {Object} options 
 * @param {number} options.delta  delta  1
 * @param {Function} options.success 
 * @param {Function} options.fail 
 * @param {Function} options.complete 
 */
function navigateBack(options) {
    let {
        delta = 1,
        success,
        fail,
        complete
    } = options;
    let navigateBackSuccess = true;
    try {
        if (!this.$router) {
            return;
        }
        delta = parseInt(delta, 10);
        let routes = this.$router.options.routes;
        delta > routes.length
            ? this.$router.push(routes[0].path)
            : this.$router.go(-delta);
    }
    catch (e) {
        navigateBackSuccess = false;
        callback(fail, e);
    }
    navigateBackSuccess && callback(success);
    callback(complete);
}

/**
 *  ,  ? = &  path?key=value&key2=value2 tabBar 
 * @param {Object} options 
 * @param {string} options.url 
 */
function reLaunch(options) {
    // tspath
    let url = options.url + (options.url.indexOf('?') > -1 ? '&' : '?') + 'ts=' + Date.now();
    location.href = location.href.replace(/\/pages\/(.*)/, url);
}

/* eslint-disable fecs-export-on-declare */
export {
    navigateTo,
    redirectTo,
    navigateBack,
    reLaunch
};
